.header {
  background: rgba(255, 255, 255, 0.9);
  left: 0;
  position: fixed;
  right: 0;
  z-index: 1;
}

.content {
  padding-top: 75px;
}

.column {
  padding-left: 0 !important;
}

.words {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
}

.words:after {
  content: "";
  flex: auto;
}

.word {
  align-items: center;
  display: flex !important;
  font-size: 1.5em !important;
  margin-top: 10px !important;
}

.wordRed {
  background-color: #e57373 !important;
  border-color: #e57373 !important;
  color: #fff !important;
}

.wordBlue {
  background-color: #5993c0 !important;
  border-color: #5993c0 !important;
  color: #fff !important;
}

.wordViolet {
  background-color: #9575cd !important;
  border-color: #9575cd !important;
  color: #fff !important;
}
